<template>
    <div class="sidebar" style="overflow:hidden;max-width:14%;">
        <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" unique-opened router>
            <template v-for="(item,index) in items">
                <template v-if="true" >
                    <el-submenu :index="item.index" :key='index'>
                        <template slot="title" ><i :class="item.icon"></i>{{ item.title }}</template>
                            <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">{{ subItem.title }}
                            </el-menu-item>
                    </el-submenu>
                </template>
            </template>
        </el-menu>
    </div>
</template>
<style >
</style>

<script>
    import vuex from '../../store/index'
    import * as carMsg from '../../api/carMsg'
    export default {
        // inject:['reload'],
        data() {
            return {
                items:[],
                items2: [
                    {
                        icon: 'el-icon-date',
                        index: '1',
                        title: '车场管理',
                        subs: [
                            {
                                index: '/testMsg',
                                title: '车场列表'
                            },
                            {
                                index:"/zcym",
                                title:"车场注册"
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-document',
                        index: '2',
                        title: '收费规则管理',
                        subs: [
                            {
                                index: '/getRule',
                                title: '收费规则列表'
                            },
                            {
                                index: '/setRule',
                                title: '规则配置'
                            },
                        ]
                    },
                    {
                        icon: 'el-icon-message',
                        index: '3',
                        title: '车辆管理',
                        subs: [
                            {
                                index: '/addCarType',
                                title: '车辆分组'
                            },
                            {
                                index: '/getCar',
                                title: '车辆查询'
                            },
                            // {
                            //     index: '/addCar',
                            //     title: '添加车辆'
                            // },
                            {
                                index: '/getMonthCar',
                                title: '月租车管理'
                            },
                            {
                                index: '/getBlackList',
                                title: '黑名单管理'
                            },
                            {
                                index: '/getWhiteList',
                                title: '白名单管理'
                            },
                           
                        ]
                    },
                    {
                        icon: 'el-icon-message',
                        index: '5',
                        title: '通行规则管理',
                        subs: [
                            {
                                index:"/carPassRule",
                                title:"车辆通行规则"
                            },
                            {
                                index:"/carTypePassRule",
                                title:"新增通行规则"
                            },
                        ]
                    },
                    {
                        icon: 'el-icon-time',
                        index: '6',
                        title: '时间管理',
                        subs: [
                            {
                                index:"/timeMsg",
                                title:"时间列表"
                            },
                            {
                                index:"/setHoliday",
                                title:"节假日定义"
                            },
                        ]
                    },
                    {
                        icon: 'el-icon-view',
                        index: '7',
                        title: '实时监控',
                        subs: [
                            {
                                index:"/crgl",
                                title:"进出口监控"
                            },
                            {
                                index:"/AllVehicle",
                                title:"过车数据"
                            },
                        ]
                    },
                ],               
                btnStatus:true,
                code:7                                                                                                                                                            
            }
        },
        computed:{
            onRoutes(){
                return this.$route.path.replace('/','');
            },
        },
      mounted:function(){
        this.getSider();
      },
    //   watch:{
    //       code(newName,oldName){
    //           console.log(oldName)
    //           console.log(newName)
    //           if(newName=='0'){
    //               OCX.OCX_DisConnect();
    //               alert("销毁成功!")
    //           }else{
    //               console.log('newName')
    //               console.log(newName)
    //           }
    //           console.log(OCX.OCX_DisConnect())
    //       }
    //     },
    //   beforeDestroy:function(){
    //     console.log("销毁了......")
    //     vuex.commit("changeConnect",false);
    //     vuex.commit("changeCode",6);
    //     document.getElementById("OCX").OCX_DisConnect();
    // },
        methods:{
            getSider(){
                var self=this;
                carMsg.bannerMsg().then(function(res){
                    console.log("返回值。。。。。。。")
                    console.log(res.data.data)
                    for(var i=0;i<res.data.data.length;i++){
                        var  itemObj={};
                        itemObj.subs=[];
                        for(var j=0;j<res.data.data[i].length;j++){
                            var subsObj={};
                            itemObj.icon=res.data.data[i][j].icon
                            itemObj.index=i+1+"";
                            itemObj.show=true;
                            itemObj.title=res.data.data[i][j].parentMenu,
                            subsObj.title=res.data.data[i][j].sonMenu;
                            subsObj.index=res.data.data[i][j].url;
                            itemObj.subs.push(subsObj);
                        }
                        self.items.push(itemObj);
                    }
                    console.log(self.items)
                })
            },
        },
    }
</script>
<style scoped>
    /* .header{
        background-color: rgb(47, 165, 237);
        -webkit-box-shadow: 0px 2px 2px 0px rgba(47, 100, 153, 0.004);
        -moz-box-shadow: 0px 2px 2px 0px rgba(47, 100, 153, 0.004);
        box-shadow: 0px 2px 2px 0px rgba(47, 100, 153, 0.004);
    }
    .el-menu{
        background-color:#4179ac !important;
    }
    .el-menu-item, .el-submenu__title{
        color:#fff !important;
    }
    .el-submenu .el-menu-item:hover, .el-submenu__title:hover,.el-menu-item:hover {
        background-color:#2f6594 !important;
    }
    .sidebar{
        display:block;
        position:absolute;
        width:200px;
        left:0;
        top:70px;
        bottom:0;
        background:#4179ac;
    } */
</style>



