<template>
    <div class="wrapper">
        <v-head></v-head>
        <div class="content">
            <transition name="move" mode="out-in"><router-view></router-view></transition>
        </div>
        <v-sidebar></v-sidebar>
    </div>
</template>

<script>
    import vHead from './Head.vue';
    import vSidebar from './Sidebar.vue';
    export default {
        components:{
            vHead, vSidebar
        },
        mounted:function(){
            for (var i = 0; i < this.$router.options.routes.length; i++) {
                if(this.$router.options.routes[i].children){
                    if (this.$router.options.routes[i].path !== this.$route.path) {
                        this.$router.push(this.$route.path)
                    } else {
                        this.$router.push(this.$router.options.routes[i].children[0].path)
                    }
                }
            }
        }
    }
</script>



