<template>
    <div class="header" style="padding:0 0 0 4%;">
        <div class="logo">
            <div class="title-box" @click='returnLogin()'>
                <span style="color:black;font-size:26px;display:block;text-align:center;position: absolute;left:45%;top:5px">快通道停车云平台</span>
            </div>
        </div>  
        <div class='user-info'>
            <el-dropdown tigger='click' @command='handleCommand'>
                <!--  class='el-dropdown-link' src='../../../static/img/user.png'-->
                <span style='cursor: pointer;'>
                    <img class='user-logo' src='../../../static/img/user.png'>
                    <b>{{username}}&nbsp;&nbsp;</b>
                    <i class='el-icon-caret-bottom'>
                    </i>
                </span>
                <el-dropdown-menu slot='dropdown'>
                    <el-dropdown-item command='loginout' @click.native='handleLoginOut'>退出登录
                    </el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>




    </div>
</template>
<script>
    export default {
        data() {
            return {
                username:window.localStorage.getItem("userName"),
            }
        },
        methods:{
            returnLogin(){
                var self=this;
                var url=this.baseUrl+'issuing-card/logout';
                console.log(url)
                self.$router.push({path:"/login"})
            },
            handleCommand(command){
                    console.log('点击了。。。')
                    console.log(command)
            },
            handleLoginOut(){
                console.log(111)
                var self=this;
                self.$router.push({path:"/login"})
            }
        },
        mounted(){
        },  
    }
</script>
<style scoped>
    .title-box{
        cursor: pointer;
    }
    .header {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        height: 70px;
        font-size: 22px;
        line-height: 70px;
        color: #fff;
        /* background:url("./../../../static/img/headbg01.png") no-repeat; */
        background:#fff;
        border-bottom:5px solid red;
    }
    .header .logo{
        float: left;
        width:320px;
        text-align: center;
    }
    .header .logo .title-box{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .header .logo img{
        margin-right: 20px;
    }
    .user-info {
        float: right;
        padding-right: 50px;
        font-size: 16px;
        color: #fff;
    }
    .user-info .el-dropdown-link{
        position: relative;
        display: inline-block;
        padding-left: 50px;
        color: #fff;
        cursor: pointer;
        vertical-align: middle;
    }
    .user-info .user-logo{
        position: absolute;
        right:80px;
        top:15px;
        width:40px;
        height:40px;
        border-radius: 50%;
        background-color: lightblue;
    }
    .el-icon-caret-bottom:before{
        content: "\E603";
    }
    .el-dropdown-menu__item{
        text-align: center;
        line-height: 36px;
        padding: 0 10px;
        margin: 0;
        cursor: pointer;
    }
    .el-dropdown-menu{
        margin: 5px 0;
        border: 1px solid #d1dbe5;
        box-shadow: 0 2px 4px rgba(0,0,0,.12), 0 0 6px rgba(0,0,0,.12);
        padding: 6px 0;
        z-index: 10;
        position: absolute;
        top: 0;
        left: 0;
        min-width: 100px;
    }
</style>



